@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,400;1,700&display=swap);
*{font-family:"Lato", sans-serif;font-weight:bold;color:#fff}body{margin:0;padding:0;min-height:100vh}.App{background:#0a68c3;background:linear-gradient(315deg, #0a68c3 26%, #00edff 70%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#0a68c3",endColorstr="#00edff",GradientType=1)}.App .header{background:#0a68c3;background:linear-gradient(153deg, #0a68c3 26%, #00edff 70%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#0a68c3",endColorstr="#00edff",GradientType=1);display:flex;justify-content:center;align-items:center;padding:5px}.App .header__icon{height:48px;width:48px}.App .header__title{font-weight:900;margin:15px;font-size:20px}@media (min-width: 415px){.App .header__title{font-size:32px}}.App .loading{display:flex;align-items:center;flex-direction:column;margin:50px}.App .loading__loader{border:8px solid transparent;border-radius:50%;border-top:8px solid #fff;width:80px;height:80px;-webkit-animation:spin 1s linear infinite;animation:spin 1s linear infinite}@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg)}}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.App .container{width:95%;margin:25px auto 0 auto;padding:50px 0}@media (min-width: 1200px){.App .container{width:90%}}.App .toggler{display:flex;margin:25px 0;justify-content:center}.App .toggler__cta,.App .items_load_cta{box-sizing:border-box;padding:10px;background-color:#fff;color:#0a68c3;font-size:18px;border:2px solid #0a68c3;outline:none;cursor:pointer;transition:0.3s}.App .toggler__cta--active,.App .items_load_cta{background-color:#0a68c3;color:#fff}.App .search-bar{width:100%;margin:0 auto 25px auto;display:flex;justify-content:center;align-items:center}.App .search-bar__input{margin:0 5px;padding:5px;color:#1a1a5d;width:60%;font-size:18px;border:1px solid #0a68c3;color:#0a68c3}.App .search-bar__input::-webkit-input-placeholder{color:#0a68c3}.App .search-bar__input:-ms-input-placeholder{color:#0a68c3}.App .search-bar__input::placeholder{color:#0a68c3}@media (min-width: 640px){.App .search-bar__input{width:40%}}.App .search-bar__input:focus,.App .search-bar__input:active{outline:none}.App .items_section{display:flex;flex-direction:column;justify-content:center;align-items:center}.App .items_list{width:100%;display:grid;grid-template-columns:100%;margin:50px 0}@media (min-width: 480px){.App .items_list{grid-template-columns:50% 50%}}@media (min-width: 900px){.App .items_list{grid-template-columns:20% 20% 20% 20% 20%}}.App .items_load_cta{margin:25px}.App .item{margin:5px;box-sizing:border-box;position:relative;cursor:pointer}.App .item__thumbnail{width:100%}.App .item__title{position:absolute;bottom:3px;border:1px solid transparent;background-color:rgba(50,50,91,0.5);margin:0;padding:10px;width:100%;box-sizing:border-box}.App .item__ratings{position:absolute;top:-10px;right:5px;background-color:rgba(50,50,91,0.5);border:1px dashed white;border-radius:50%;padding:5px;font-weight:bold;min-width:25px;min-height:25px;display:flex;align-items:center;justify-content:center}

